import { Inject, Injectable } from '@angular/core';
import { WINDOW, LocalWindow } from '../model/providers';

@Injectable()
export class DownloadResourcesService {
  constructor(@Inject(WINDOW) private readonly localWindow: LocalWindow) {}

  downloadFile(name: string, type: string, data: BlobPart) {
    const fileData = new Blob([data], { type });
    if (this.isFileSaverAvailable()) {
      this.downloadViaFileSaver(name, fileData);
    } else {
      this.downloadViaLink(name, fileData);
    }
  }

  private isFileSaverAvailable(): boolean {
    const { navigator } = this.localWindow;
    // @ts-ignore
    return navigator !== undefined && navigator.msSaveOrOpenBlob !== undefined;
  }

  private downloadViaFileSaver(name: string, data: Blob) {
    const { navigator } = this.localWindow;
    // @ts-ignore
    navigator.msSaveOrOpenBlob(data, name);
  }

  private downloadViaLink(name: string, data: Blob) {
    const { document, URL } = this.localWindow;
    const downloadLink = document.createElement('a');
    downloadLink.href = URL.createObjectURL(data);
    downloadLink.download = name;
    downloadLink.click();
    downloadLink.remove();
  }
}
