import { InjectionToken } from '@angular/core';
import { AccountAliasDisplayingLevel } from './types';

export type LocalWindow = Window & typeof globalThis;

export const WINDOW = new InjectionToken<string>('ProductSummaryCommon :: WindowToken');

export const windowFactory = () => window;

export const WindowProvider = {
  provide: WINDOW,
  useFactory: windowFactory,
};

/**
 * Injection token for widget properties configuration
 */
export const PRODUCT_SUMMARY_WIDGET_PROPERTIES = new InjectionToken('ProductSummaryCommon :: WidgetProperties');

/**
 * Injection token for account alias displaying level configuration
 */
export const ACCOUNT_ALIAS_DISPLAYING_LEVEL = new InjectionToken<AccountAliasDisplayingLevel>(
  'ProductSummaryCommon :: AccountAliasDisplayingLevel',
);
