import { Injectable } from '@angular/core';
import { HttpErrorResponse, HttpHeaders, HttpResponse } from '@angular/common/http';
import {
  AccountBalanceHistory,
  AccountBalanceHistoryItem,
  AccountsHttpService,
  GetBalanceHistoryForArrangementRequestParams,
  GetBalanceHistoryRequestParams,
  TimePeriod,
} from '@backbase/data-ang/arrangements';
import { BehaviorSubject, Observable, of, ReplaySubject } from 'rxjs';
import { catchError, distinctUntilChanged, filter, map, scan, switchMap, tap } from 'rxjs/operators';
import {
  BalanceHistoryDataSeries,
  BalanceHistoryExportData,
  BalanceHistoryExportFormat,
  BalanceHistoryPaginated,
  BalanceHistoryPaginationParams,
  BalanceHistorySeriesParams,
  GetBalanceHistoryRequestPaginatedObjectType,
  GetBalanceHistoryRequestSeriesObjectType,
  HttpResponseType,
  PaginationType,
} from '../../model/types';
import { defaultArrangementName } from '../../model/constants';
import { splitDataByTrend } from './colors-helper';
import {
  BalanceHistoryError,
  parseBalanceHistoryError,
  /**
   * these unused imports here because of issue in api-extractor
   * issue: https://github.com/microsoft/rushstack/issues/2140
   */
  /* eslint-disable @typescript-eslint/no-unused-vars,no-unused-vars */
  BalanceHistoryAccessDenied,
  BalanceHistoryBadRequest,
  BalanceHistoryNotFound,
  BalanceHistoryConnectivityError,
  BalanceHistoryUnknownError,
  /* eslint-enable */
} from '../../errors/balance-history-service-errors';

@Injectable()
export class BalanceHistoryCommonService {
  private readonly reportDefaultFileName = {
    [BalanceHistoryExportFormat.CSV]: 'balance-history.csv',
    [BalanceHistoryExportFormat.JSON]: 'balance-history.json',
  };
  private readonly requestObjectSeries = new ReplaySubject<GetBalanceHistoryRequestSeriesObjectType>(1);
  private readonly requestObjectPaginated = new ReplaySubject<GetBalanceHistoryRequestPaginatedObjectType>(1);

  constructor(private readonly accountDataService: AccountsHttpService) {}

  readonly errorSeries = new BehaviorSubject<BalanceHistoryError | undefined>(undefined);
  readonly errorPaginated = new BehaviorSubject<BalanceHistoryError | undefined>(undefined);

  readonly loadingPaginated = new BehaviorSubject<boolean>(true);
  readonly loadingSeries = new BehaviorSubject<boolean>(true);

  readonly balanceHistorySeries: Observable<AccountBalanceHistory | undefined> = this.requestObjectSeries.pipe(
    distinctUntilChanged(),
    filter(param => !!param.arrangementIds),
    tap(() => this.loadingSeries.next(true)),
    tap(() => this.errorSeries.next(undefined)),
    switchMap(res =>
      this.getBalanceHistorySeries(res).pipe(
        catchError((errorSeries: BalanceHistoryError) => {
          this.errorSeries.next(errorSeries);
          return of(undefined);
        }),
      ),
    ),
    tap(() => this.loadingSeries.next(false)),
  );

  readonly balanceHistoryPaginated: Observable<BalanceHistoryPaginated | undefined> = this.requestObjectPaginated.pipe(
    distinctUntilChanged(),
    filter(param => !!param.arrangementId),
    tap(() => this.loadingPaginated.next(true)),
    tap(() => this.errorPaginated.next(undefined)),
    switchMap(res =>
      this.getBalanceHistoryPaginated(res).pipe(
        catchError((errorPaginated: BalanceHistoryError) => {
          this.errorPaginated.next(errorPaginated);
          return of({
            count: 0,
            items: [],
            params: res,
          });
        }),
      ),
    ),
    scan(
      (acc: BalanceHistoryPaginated, curr: BalanceHistoryPaginated): BalanceHistoryPaginated => ({
        count: curr.count,
        items: this.responsesMerge(acc, curr),
        params: curr.params,
      }),
    ),
    tap(() => this.loadingPaginated.next(false)),
  );

  getBalanceHistorySeries(request: GetBalanceHistoryRequestSeriesObjectType) {
    const parameters = this.getBalanceHistoryRequestParameters(request);
    return this.accountDataService.getBalanceHistory(parameters).pipe(
      catchError((error: HttpErrorResponse) => {
        throw parseBalanceHistoryError(error);
      }),
    );
  }

  getBalanceHistoryPaginated(request: GetBalanceHistoryRequestPaginatedObjectType) {
    const paginationType = request.params ? request.params.paginationType || '' : '';
    const from = request.params ? request.params.from || 0 : 0;
    const parameters = this.getBalanceHistoryForArrangementRequestParameters(request);
    return this.accountDataService.getBalanceHistoryForArrangement(parameters, HttpResponseType.RESPONSE).pipe(
      map((res: HttpResponse<Array<AccountBalanceHistoryItem>>) =>
        this.mapResponseWithCount(res, { from, paginationType }),
      ),
      catchError((error: HttpErrorResponse) => {
        throw parseBalanceHistoryError(error);
      }),
    );
  }

  loadBalanceHistorySeries(requestObjectSeries: Observable<GetBalanceHistoryRequestSeriesObjectType>) {
    requestObjectSeries.subscribe(this.requestObjectSeries);
  }

  loadBalanceHistoryPaginated(requestObjectPaginated: Observable<GetBalanceHistoryRequestPaginatedObjectType>) {
    requestObjectPaginated.subscribe(this.requestObjectPaginated);
  }

  /**
   * Returns URL for fetching balance history data
   *
   * @param format
   * @param exportParameters
   */
  exportBalanceHistory(
    format: BalanceHistoryExportFormat,
    exportParameters: GetBalanceHistoryRequestSeriesObjectType,
  ): string {
    const parameters = this.getBalanceHistoryRequestParameters(exportParameters, format);
    return this.accountDataService.getBalanceHistoryUrl(parameters);
  }

  /**
   * Fetches balance history data in one of the available formats
   *
   * @param format
   * @param exportParameters
   */
  getBalanceHistory(
    format: BalanceHistoryExportFormat,
    exportParameters: GetBalanceHistoryRequestSeriesObjectType,
  ): Observable<BalanceHistoryExportData> {
    const parameters = this.getBalanceHistoryRequestParameters(exportParameters, format);
    const options = { httpHeaderAccept: format as BalanceHistoryExportFormat.CSV };

    return this.accountDataService.getBalanceHistory(parameters, HttpResponseType.RESPONSE, false, options).pipe(
      map(({ headers, body }: HttpResponse<any>) => {
        const defaultFileName = this.reportDefaultFileName[format];
        const fileNameInHeaders = this.getFileNameFromHeaders(headers);
        return { name: fileNameInHeaders || defaultFileName, content: body };
      }),
    );
  }

  private responsesMerge(
    acc: BalanceHistoryPaginated,
    current: BalanceHistoryPaginated,
  ): Array<AccountBalanceHistoryItem> {
    if (
      acc &&
      acc.items &&
      current &&
      current.items &&
      acc.params.paginationType === PaginationType.LOAD_MORE &&
      current.params.from !== 0
    ) {
      return [...acc.items, ...current.items];
    }

    return current.items;
  }

  processBalanceHistory(balanceHistory: AccountBalanceHistory): BalanceHistoryDataSeries[] {
    const [balanceHistoryItem] = balanceHistory.items;
    const defaultSeries = {
      name: defaultArrangementName,
      series: balanceHistoryItem.balanceHistory.map(({ dateTo, value, valuePtc }) => ({
        name: new Date(dateTo),
        value: ((value ? value.balance : valuePtc) as unknown) as number,
      })),
    };

    return defaultSeries ? splitDataByTrend(defaultSeries.series) : [];
  }

  private mapResponseWithCount(
    response: HttpResponse<Array<AccountBalanceHistoryItem>>,
    { from, paginationType }: { from: number; paginationType: PaginationType | string },
  ): BalanceHistoryPaginated {
    // eslint-disable-next-line no-null/no-null
    if (response.body === null) {
      throw new Error();
    }
    const headerCount = response.headers ? response.headers.get('x-total-count') : undefined;
    const counter = headerCount ? parseInt(headerCount, 10) : response.body.length || 0;

    return {
      count: counter,
      items: response.body,
      params: { from, paginationType },
    };
  }

  private getBalanceHistoryRequestParameters(
    { arrangementIds, params }: GetBalanceHistoryRequestSeriesObjectType,
    requestFormat?: string,
  ): GetBalanceHistoryRequestParams {
    const format = requestFormat ?? params.format;
    return { arrangementIds, ...this.toRequestParameters({ ...params, format }) };
  }

  private getBalanceHistoryForArrangementRequestParameters({
    arrangementId,
    params,
  }: GetBalanceHistoryRequestPaginatedObjectType): GetBalanceHistoryForArrangementRequestParams {
    const format = undefined;
    return { arrangementId, ...this.toRequestParameters({ ...params, format }) };
  }

  private toRequestParameters(
    params: BalanceHistorySeriesParams | BalanceHistoryPaginationParams,
  ): Partial<GetBalanceHistoryRequestParams | GetBalanceHistoryForArrangementRequestParams> {
    const requestParameters = { ...params, timePeriod: params.timePeriod as TimePeriod | undefined };
    delete requestParameters.paginationType;
    if (!requestParameters.format) {
      delete requestParameters.format;
    }
    if (requestParameters.timePeriod) {
      delete requestParameters.dateFrom;
      delete requestParameters.dateTo;
    } else {
      delete requestParameters.timePeriod;
    }
    return requestParameters;
  }

  private getFileNameFromHeaders(headers?: HttpHeaders): string | undefined {
    const contentDisposition = (headers && headers.get('content-disposition')) || '';
    const contentDispositionWithoutQuotes = contentDisposition.replace(/['"]*/g, '');
    return (contentDispositionWithoutQuotes.match(/filename[^;=\n]*=([^;\n]*)/) || [])[1];
  }
}
