import { Component, EventEmitter, Input, Output } from '@angular/core';
import { MaskableAttribute } from '@backbase/data-ang/arrangements';
import {
  ProductDetailsAvailability,
  ProductMaskableAttribute,
  AccountArrangementItem,
} from '../../model/product-summary-types';

@Component({ template: '' })
export class ProductSummaryAccountDetailsBaseComponent {
  /**
   * Product type data that this component displays.
   */
  @Input() set product(data: AccountArrangementItem | undefined) {
    this._product = data;
    this.infoAvailability = this.getProductInfoAvailability(data);
  }
  get product(): AccountArrangementItem | undefined {
    return this._product;
  }
  private _product: AccountArrangementItem | undefined;
  /**
   * List of maskable attributes. Holds states of the attributes.
   */
  @Input() maskableAttributes: ProductMaskableAttribute[] = [];
  /**
   * Event when a user clicked on the `unmask-attribute-button` button
   */
  @Output() unmaskAttribute: EventEmitter<MaskableAttribute> = new EventEmitter<MaskableAttribute>();
  /**
   * Event when a user clicked on the `mask-attribute-button` button
   */
  @Output() maskAttribute: EventEmitter<MaskableAttribute> = new EventEmitter<MaskableAttribute>();

  /**
   * All unmaskable attributes
   */
  readonly maskableAttribute = MaskableAttribute;
  /**
   * Details info sections availability
   */
  infoAvailability: ProductDetailsAvailability = {};

  /**
   * Returns `true` if the given `attributeName` is masked
   *
   * @param attributeName
   */
  isMasked(attributeName: MaskableAttribute): boolean {
    const maskableAttribute = this.maskableAttributes.find(attribute => attribute.attributeName === attributeName);
    return maskableAttribute?.masked === undefined ? true : maskableAttribute?.masked;
  }
  /**
   * Event handler for the `click` event of the unmask buttons
   * Emits `MaskableAttribute` to the `unmaskAttribute` Output
   *
   * @param attributeName
   */
  onUnmaskAttribute(attributeName: MaskableAttribute) {
    if (Object.values(MaskableAttribute).includes(attributeName)) {
      this.unmaskAttribute.emit(attributeName);
    }
  }
  /**
   * Event handler for the `click` event of the mask buttons
   * Emits `MaskableAttribute` to the `maskAttribute` Output
   *
   * @param attributeName
   */
  onMaskAttribute(attributeName: MaskableAttribute) {
    if (Object.values(MaskableAttribute).includes(attributeName)) {
      this.maskAttribute.emit(attributeName);
    }
  }

  // eslint-disable-next-line @typescript-eslint/no-unused-vars,no-unused-vars
  protected getProductInfoAvailability(product: AccountArrangementItem | undefined): ProductDetailsAvailability {
    return {};
  }
}
