import { DataSet, Series } from '../../model/types';

export type Trend = 1 | -1;

export const getColorTrend = (index: number, series: Series[]): Trend => {
  const serie = series[index];
  if (serie.value === 0) {
    if (index > 0) {
      return getColorTrend(index - 1, series);
    }

    if (series.length > 1) {
      return series[1].value < 0 ? -1 : 1;
    }
  }

  if (serie.value < 0) {
    return -1;
  }

  return 1;
};

export const getCurrentSeriesName = (trend: Trend): 'positive' | 'negative' => (trend === -1 ? 'negative' : 'positive');

export const getFirstSeries = (datum: Series, trend: Trend): Array<DataSet> => [
  {
    name: getCurrentSeriesName(trend),
    series: [datum],
  },
];

export const createNewSeries = (
  result: Array<DataSet>,
  lastResult: DataSet,
  series: Series,
  trend: Trend,
): Array<DataSet> => {
  const lastName: Date = lastResult.series[lastResult.series.length - 1].name;
  const lastValue: number = lastResult.series[lastResult.series.length - 1].value;
  const valuesSum: number = Math.abs(lastValue) + Math.abs(series.value);
  const step: number = series.name.getTime() - lastName.getTime();
  const valuesRatio: number = Math.abs(lastValue) / valuesSum;
  const prevValue = lastResult.series[lastResult.series.length - 1].value;
  const crossingPoint: Series = { name: new Date(lastName.getTime() + step * valuesRatio), value: 0 };
  const crossPointSection = prevValue ? [crossingPoint] : [];

  return [
    ...result.slice(0, -1),
    {
      name: lastResult.name,
      series: [...lastResult.series, ...crossPointSection],
    },
    {
      name: getCurrentSeriesName(trend),
      series: [crossingPoint, series],
    },
  ];
};

export const concatExistingSeries = (result: Array<DataSet>, lastResult: DataSet, series: Series): Array<DataSet> => {
  const name = lastResult.name;
  return [
    ...result.slice(0, -1),
    {
      name,
      series: [...lastResult.series, series],
    },
  ];
};

export const splitDataByTrend = (series: Array<Series>): Array<DataSet> =>
  series.reduce((result: Array<DataSet>, seriesData: Series, i: number) => {
    const currentTrend = getColorTrend(i, series);

    if (i === 0) {
      return getFirstSeries(seriesData, currentTrend);
    }

    const previousTrend = getColorTrend(i - 1, series);
    const lastResult = result[result.length - 1];

    if (previousTrend === currentTrend) {
      return concatExistingSeries(result, lastResult, seriesData);
    } else {
      return createNewSeries(result, lastResult, seriesData, currentTrend);
    }
  }, []);
