import { HttpErrorResponse } from '@angular/common/http';

export class AccountsOverviewNotFound {
  messages: string[];
  kind: 'AccountsOverviewNotFound' = 'AccountsOverviewNotFound';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class AccountsOverviewBadRequest {
  messages: string[];
  kind: 'AccountsOverviewBadRequest' = 'AccountsOverviewBadRequest';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class AccountsOverviewAccessDenied {
  messages: string[];
  kind: 'AccountsOverviewAccessDenied' = 'AccountsOverviewAccessDenied';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class AccountsOverviewUnknownError {
  messages: string[];
  kind: 'AccountsOverviewUnknownError' = 'AccountsOverviewUnknownError';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = [response.message];
  }
}

export class AccountsOverviewConnectivityError {
  messages: string[];
  kind: 'AccountsOverviewConnectivityError' = 'AccountsOverviewConnectivityError';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export type AccountsOverviewError =
  | AccountsOverviewAccessDenied
  | AccountsOverviewBadRequest
  | AccountsOverviewNotFound
  | AccountsOverviewConnectivityError
  | AccountsOverviewUnknownError;

export const parseError = (error: HttpErrorResponse): AccountsOverviewError => {
  if (error.error instanceof ErrorEvent) {
    return new AccountsOverviewUnknownError(error);
  } else {
    switch (error.status) {
      case 0:
        return new AccountsOverviewConnectivityError(error);
      case 401:
        return new AccountsOverviewAccessDenied(error);
      case 403:
        return new AccountsOverviewAccessDenied(error);
      case 400:
        return new AccountsOverviewBadRequest(error);
      case 404:
        return new AccountsOverviewNotFound(error);
      default:
        return new AccountsOverviewUnknownError(error);
    }
  }
};
