import { Pipe, PipeTransform } from '@angular/core';

/**
 * Verifies if the passed item is presented in the array
 * Note: deep-equal is not supported
 *
 * @usageNotes
 * ### Example
 * ```html
 * <div *ngIf="{{ item|bbIsArray:array }}"></div>
 * ```
 */
@Pipe({ name: 'bbIsInArray' })
export class IsInArrayPipe implements PipeTransform {
  /**
   * Verifies if `item` is presented in `array`
   *
   * @param item
   * @param array
   */
  transform<T>(item: T, array: T[]): boolean {
    if (array && array.length) {
      return array.indexOf(item) !== -1;
    }
    return false;
  }
}
