/* eslint-disable @typescript-eslint/ban-types */
export type PlainValue<T> = T extends string | number | boolean | null | undefined
  ? T
  : T extends Function
  ? never
  : T extends object
  ? { [K in keyof T]: PlainValue<T[K]> }
  : never;
/* eslint-enable */

// eslint-disable-next-line no-null/no-null
export const isPlainObject = <T>(obj: PlainValue<T>): boolean => typeof obj === 'object' && obj !== null;

/**
 * Equality check between two "plain" (i.e. JSON compatible) values.
 * By "plain" value, we mean a primitive or an object/array that contains only other primitives and no methods or internal state.
 */
export const isEqual = <A extends unknown, B extends unknown>(a: PlainValue<A>, b: PlainValue<B>): boolean => {
  if (Array.isArray(a) && Array.isArray(b)) {
    return a.length === b.length && a.every((aValue, i) => isEqual(aValue, b[i]));
  }
  if (Array.isArray(a) || Array.isArray(b)) {
    return false;
  }

  if (isPlainObject(a) && isPlainObject(b)) {
    const aObject = a as PlainValue<Record<string, PlainValue<any>>>;
    const bObject = b as PlainValue<Record<string, PlainValue<any>>>;
    const aKeys = Object.keys(aObject);
    const bKeys = Object.keys(bObject);
    return aKeys.length === bKeys.length && aKeys.every(aKey => isEqual(aObject[aKey], bObject[aKey]));
  }

  return a === b;
};
