import { Observable, of, combineLatest, BehaviorSubject } from 'rxjs';
import { map } from 'rxjs/operators';
import { ItemModel } from '@backbase/foundation-ang/core';
import { Property } from '@backbase/foundation-ang/web-sdk';
import { Inject, Injectable, Optional } from '@angular/core';
import { PRODUCT_SUMMARY_WIDGET_PROPERTIES } from '../../model/providers';

/** @dynamic */
@Injectable()
export class ProductSummaryWidgetPropertiesService<
  WidgetProperties extends Record<string, any> = Record<string, Property>
> {
  /**
   * Widget properties
   */
  readonly properties: Observable<WidgetProperties>;
  /**
   * Widget properties configuration
   */
  private readonly configuration = new BehaviorSubject<Partial<WidgetProperties>>({});

  /**
   * @internal
   * @param defaultConfiguration
   * @param itemModel
   */
  constructor(
    @Inject(PRODUCT_SUMMARY_WIDGET_PROPERTIES) private readonly defaultConfiguration: WidgetProperties,
    @Optional() private readonly itemModel?: ItemModel,
  ) {
    /**
     * Gets a property value for each value in `propertyNames` input,
     * joins them into `WidgetProperties` structure
     */
    const propertyNames = Object.keys(defaultConfiguration);
    this.properties = combineLatest(propertyNames.map(propertyName => this.getWidgetProperty(propertyName))).pipe(
      map(properties =>
        propertyNames.reduce(
          (widgetProperties, propertyName, index) => ({ ...widgetProperties, [propertyName]: properties[index] }),
          {} as WidgetProperties,
        ),
      ),
    );
  }

  /**
   * Sets widget properties
   *
   * @param config
   */
  setConfiguration(config: Partial<WidgetProperties>) {
    this.configuration.next(config);
  }

  /**
   * Returns observable with a widget property
   *
   * @param propertyName
   */
  getProperty<
    WidgetPropertyName extends keyof WidgetProperties,
    WidgetPropertyValue extends WidgetProperties[WidgetPropertyName]
  >(propertyName: WidgetPropertyName): Observable<WidgetPropertyValue> {
    return this.properties.pipe(map(properties => properties[propertyName]));
  }

  /**
   * Returns a widget configuration property,
   * takes the value in the following order:
   * - configuration value if exists (widget input)
   * - item-model value if exists (CXP configuration)
   * - default value (constant value)
   */
  private getWidgetProperty<
    WidgetPropertyName extends keyof WidgetProperties,
    WidgetPropertyValue extends WidgetProperties[WidgetPropertyName]
  >(propertyName: WidgetPropertyName): Observable<WidgetPropertyValue> {
    const defaultValue = this.defaultConfiguration[propertyName];
    const configurationValue$ = this.configuration.pipe(
      map(configuration => configuration[propertyName] as WidgetPropertyValue | undefined),
    );
    const modelValue$ = this.itemModel
      ? this.itemModel.property(propertyName as string, defaultValue)
      : of(defaultValue);

    return combineLatest([configurationValue$, modelValue$]).pipe(
      map(([configurationValue, modelValue]) => (configurationValue !== undefined ? configurationValue : modelValue)),
    );
  }
}
