import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { map, pluck } from 'rxjs/operators';
import {
  FinancialInstitutionManagerClientHttpService,
  FinancialInstitution,
} from '@backbase/data-ang/financial-institution-manager';
import { Arrangement, ExternalProductInfo } from '../../model/product-summary-types';
import { hasExternalInfo } from '../../helpers/helpers';

/**
 * A service for getting external info for products
 */
@Injectable()
export class ProductsExternalInfoService {
  constructor(private readonly financialInstitutionsHttpService: FinancialInstitutionManagerClientHttpService) {}

  /**
   * Fetches external info for products or a single product,
   * returns products or a single product with external info included
   */
  getProductsWithExternalInfo<T extends ExternalProductInfo = Arrangement>(products: T): Observable<T>;
  getProductsWithExternalInfo<T extends ExternalProductInfo = Arrangement>(products: T[]): Observable<T[]>;
  getProductsWithExternalInfo<T extends ExternalProductInfo = Arrangement>(products: T | T[]): Observable<T | T[]> {
    const productsList = Array.isArray(products) ? products : [products];
    const financialInstitutionIds = this.getAssignedFinancialInstitutionIds(productsList);

    return financialInstitutionIds.length
      ? this.getFinancialInstitutions(financialInstitutionIds).pipe(
          map(institutions =>
            Array.isArray(products)
              ? productsList.map(product => this.addExternalInfoToProduct(product, institutions))
              : this.addExternalInfoToProduct(products, institutions),
          ),
        )
      : of(products);
  }

  private getAssignedFinancialInstitutionIds<T extends ExternalProductInfo = Arrangement>(items: T[]): number[] {
    const itemsWithExternalInfo = items.filter(hasExternalInfo);
    const financialInstitutionIds = itemsWithExternalInfo.map(item => item.financialInstitutionId) as number[];
    return financialInstitutionIds.filter((item, index) => financialInstitutionIds.indexOf(item, index + 1) === -1);
  }

  private addExternalInfoToProduct<T extends ExternalProductInfo = Arrangement>(
    product: T,
    institutions: FinancialInstitution[],
  ): T {
    return { ...product, ...this.getExternalInfo(product, institutions) };
  }

  private getExternalInfo<T extends ExternalProductInfo = Arrangement>(
    product: T,
    institutions: FinancialInstitution[],
  ): ExternalProductInfo {
    const isExternalInfoAvailable = hasExternalInfo(product);
    return isExternalInfoAvailable
      ? { financialInstitution: institutions.find(institution => institution.id === product.financialInstitutionId) }
      : {};
  }

  private getFinancialInstitutions(ids: number[]): Observable<FinancialInstitution[]> {
    return this.financialInstitutionsHttpService.getFinancialInstitutions({ ids }).pipe(pluck('financialInstitutions'));
  }
}
