import { HttpErrorResponse } from '@angular/common/http';

export class BalanceHistoryNotFound {
  messages: string[];
  kind: 'BalanceHistoryNotFound' = 'BalanceHistoryNotFound';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class BalanceHistoryBadRequest {
  messages: string[];
  kind: 'BalanceHistoryBadRequest' = 'BalanceHistoryBadRequest';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class BalanceHistoryAccessDenied {
  messages: string[];
  kind: 'BalanceHistoryAccessDenied' = 'BalanceHistoryAccessDenied';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class BalanceHistoryUnknownError {
  messages: string[];
  kind: 'BalanceHistoryUnknownError' = 'BalanceHistoryUnknownError';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = [response.message];
  }
}

export class BalanceHistoryConnectivityError {
  messages: string[];
  kind: 'BalanceHistoryConnectivityError' = 'BalanceHistoryConnectivityError';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export type BalanceHistoryError =
  | BalanceHistoryAccessDenied
  | BalanceHistoryBadRequest
  | BalanceHistoryNotFound
  | BalanceHistoryConnectivityError
  | BalanceHistoryUnknownError;

export const parseBalanceHistoryError = (error: HttpErrorResponse): BalanceHistoryError => {
  if (error.error instanceof ErrorEvent) {
    return new BalanceHistoryUnknownError(error);
  } else {
    switch (error.status) {
      case 0:
        return new BalanceHistoryConnectivityError(error);
      case 401:
        return new BalanceHistoryAccessDenied(error);
      case 403:
        return new BalanceHistoryAccessDenied(error);
      case 400:
        return new BalanceHistoryBadRequest(error);
      case 404:
        return new BalanceHistoryNotFound(error);
      default:
        return new BalanceHistoryUnknownError(error);
    }
  }
};
