import { publishReplay, refCount, switchMap } from 'rxjs/operators';
import { Observable, combineLatest } from 'rxjs';
import { map, pluck } from 'rxjs/operators';
import { CardProductKindName, DefaultProductKindNameList } from '../model/constants';
import { AccountNumberType } from '@backbase/ui-ang/account-number-pipe';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Arrangement } from '../model/product-summary-types';

export const toKebabCase = (accountType = '') =>
  accountType ? accountType.trim().toLowerCase().split(' ').join('-') : '';

export const isCardNumber = (account: Arrangement): boolean =>
  Boolean(account.productKindName && CardProductKindName.includes(account.productKindName.toLowerCase()));

export const modelItemToBoolean = (value: string | boolean): boolean => {
  if (typeof value === 'boolean') {
    return value;
  }
  return value === 'true';
};

export const isFavorite = (account: Arrangement) => {
  const accountPreferencesFavoriteValue = account.userPreferences && account.userPreferences.favorite;
  const accountFavoriteValue = 'favorite' in account ? account.favorite : undefined;

  return accountPreferencesFavoriteValue ?? accountFavoriteValue;
};

export const isProductVisible = (product: Arrangement): boolean => {
  if (product.userPreferences && product.userPreferences.visible !== undefined) {
    return product.userPreferences.visible;
  } else if (product.visible !== undefined) {
    return product.visible;
  }
  return true;
};

export const isFunction = (param: any) => typeof param === 'function';

export const findBy = <T = any>(collection: T[], key: string, value: string): T | undefined =>
  collection.find(item => item && (item as any)[key] === value) as T;

export const findByName = <T = any>(collection: T[], name: string): T | undefined => findBy(collection, 'name', name);

export const getAccountAlias = (account: any): string | undefined => {
  if (account.userPreferences) {
    return account.userPreferences.alias ? account.userPreferences.alias : account.bankAlias || account.name;
  } else {
    return account.bankAlias || account.name || ''; // todo: replace with `??` typesScript is upgraded to 3.7.0
  }
};

export const getProductKindIconName = (productKindName: string): string => {
  let iconName = toKebabCase(productKindName);
  const isDefaultProductKindName = Object.values(DefaultProductKindNameList).some(name => iconName === name);

  if (!isDefaultProductKindName) {
    iconName = 'account';
  }

  return `${iconName}s`;
};

/**
 * Gets a valid product number type for a given product
 *
 * @param product
 *
 * for CreditCard/DebitCard:
 * - returns `number` if exists
 * - else `IBAN` if exists
 * - otherwise `BBAN`
 * for other products:
 * - returns `IBAN` if exists
 * - else `BBAN` if exists
 * - otherwise `number`
 */
export const getProductNumberType = (product: Arrangement): AccountNumberType => {
  const isCardFormat = isCardNumber(product);

  if (isCardFormat && 'number' in product) {
    return 'cardNumber';
  } else if ('IBAN' in product) {
    return 'iban';
  } else if ('BBAN' in product || isCardFormat) {
    return 'bban';
  } else {
    return 'cardNumber';
  }
};

export const processErrorMessages = (messages: string[] | undefined): string | undefined =>
  messages ? messages.join(' ') : undefined;

/**
 * Sets the passed unmasked attribute to the item
 *
 * @param item
 * @param attributeName
 * @param attributeValue
 */
// eslint-disable-next-line @typescript-eslint/ban-types
export const setAttribute = <T = Object>(item: T, attributeName: string, attributeValue: any): T => ({
  ...(item ? item : ({} as T)),
  ...(attributeName ? { [attributeName]: attributeValue } : {}),
});

/**
 * Verifies if values is defined (not null and not undefined)
 *
 * @param value
 */
// eslint-disable-next-line no-null/no-null
export const isDefined = (value: any): boolean => value !== null && value !== undefined;
/**
 * Returns configuration property based on the priority
 */
export const filterInputParameters = <T>(input: Observable<T | undefined>, prop: Observable<T>): Observable<T> =>
  combineLatest([input, prop]).pipe(map(([inputVal, propVal]) => (inputVal !== undefined ? inputVal : propVal)));

/**
 * Caches the request (shares between subscribers)
 *
 * @param request
 */
export const cacheRequest = <Params, Result>(request: (params: Params) => Observable<Result>) => (
  source: Observable<Params>,
): Observable<Result> => source.pipe(switchMap(request), publishReplay(1), refCount());

/**
 * Returns route parameter
 * takes parameter from component route if parameter exists, otherwise looks for parameter in parent routes
 *
 * @param route
 * @param param
 */
export const getRouteParameter = (route: ActivatedRoute, param: string): Observable<string> => {
  const routeParam = route.paramMap.pipe(pluck<ParamMap, string | null>('params', param));
  const allParams = route.parent ? [routeParam, getRouteParameter(route.parent, param)] : [routeParam];

  return combineLatest(allParams).pipe(
    map(params => params[0] || params[1]),
    map(value => value ?? ''),
  );
};

/**
 * Returns true if product has external info (external logo, alias, etc.)
 *
 * @param product
 */
export const hasExternalInfo = (product: Arrangement): boolean => product.financialInstitutionId !== undefined;
