import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { GetUserPrivilegesRequestParams, UsersHttpService } from '@backbase/data-ang/accesscontrol';
import { AccountNavigationType, ProductSummaryPrivilege } from '../../model/types';
import { DefaultAllowedNavigationTypes } from '../../model/constants';

@Injectable()
export class ProductSummaryUserPermissionsService {
  private readonly productSummaryPermissionParameters: GetUserPrivilegesRequestParams = {
    resourceName: 'Product Summary',
    functionName: 'Product Summary',
  };
  private readonly manageArrangementsPermissionParameters: GetUserPrivilegesRequestParams = {
    resourceName: 'Product Summary',
    functionName: 'Manage Arrangement Alias',
  };

  private readonly productSummaryPrivileges: Observable<
    ProductSummaryPrivilege[]
  > = this.usersDataService.getUserPrivileges(this.productSummaryPermissionParameters).pipe(
    map(privileges => privileges.map(({ privilege }) => privilege as ProductSummaryPrivilege)),
    shareReplay(),
  );
  private readonly managerArrangementsPrivileges: Observable<
    ProductSummaryPrivilege[]
  > = this.usersDataService.getUserPrivileges(this.manageArrangementsPermissionParameters).pipe(
    map(privileges => privileges.map(({ privilege }) => privilege as ProductSummaryPrivilege)),
    shareReplay(),
  );

  readonly hasFullViewPermission: Observable<boolean> = this.productSummaryPrivileges.pipe(
    map(privileges => privileges.includes(ProductSummaryPrivilege.VIEW)),
    shareReplay(),
  );

  readonly hasFullEditPermission: Observable<boolean> = this.productSummaryPrivileges.pipe(
    map(privileges => privileges.includes(ProductSummaryPrivilege.EDIT)),
    shareReplay(),
  );

  readonly hasArrangementAliasEditPermission: Observable<boolean> = this.managerArrangementsPrivileges.pipe(
    map(privileges => privileges.includes(ProductSummaryPrivilege.EDIT)),
    shareReplay(),
  );

  readonly allowedAccountNavigationTypes: Observable<AccountNavigationType[]> = this.hasFullViewPermission.pipe(
    map(hasFullViewPermission =>
      hasFullViewPermission
        ? DefaultAllowedNavigationTypes
        : DefaultAllowedNavigationTypes.filter(type => type !== AccountNavigationType.BALANCE_HISTORY),
    ),
    shareReplay(),
  );

  constructor(private readonly usersDataService: UsersHttpService) {}
}
