import { Injectable, TemplateRef, Inject } from '@angular/core';
import {
  ProductSummaryHttpService,
  ArrangementsHttpService,
  BalancesHttpService,
  AccountUserPreferences,
  AggregatedBalances,
  UnmaskedAttributeRequestParams,
} from '@backbase/data-ang/arrangements';
import { NotificationService } from '@backbase/ui-ang/notification';
import { Accounts, GetProductsummaryRequest, HttpResponseType, PaginationType } from '../../model/types';
import { BehaviorSubject, combineLatest, Observable, of, ReplaySubject } from 'rxjs';
import { catchError, distinctUntilChanged, map, scan, switchMap, take, tap } from 'rxjs/operators';
import { HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { Pubsub, PUBSUB } from '@backbase/foundation-ang/web-sdk';
import { AccountAggregatedBalanceCurrency } from '@backbase/data-ang/arrangements';
import { AccountArrangementItem, ProductSummaryItem } from '../../model/product-summary-types';
import {
  AccountsOverviewError,
  parseError,
  /**
   * these unused imports here because of issue in api-extractor
   * issue: https://github.com/microsoft/rushstack/issues/2140
   */
  /* eslint-disable @typescript-eslint/no-unused-vars,no-unused-vars */
  AccountsOverviewAccessDenied,
  AccountsOverviewBadRequest,
  AccountsOverviewNotFound,
  AccountsOverviewConnectivityError,
  AccountsOverviewUnknownError,
  /* eslint-enable */
} from '../../errors/accounts-overview-service-error';

export const bbEventToggleAccountFavoriteUpdate = 'bb.event.toggle-account-favorite.update';

@Injectable()
export class ProductSummaryAccountsService {
  constructor(
    private readonly productSummaryDataService: ProductSummaryHttpService,
    private readonly accountDataService: ArrangementsHttpService,
    private readonly balancesHttpService: BalancesHttpService,
    private readonly notificationService: NotificationService,
    @Inject(PUBSUB) private readonly pubSub: Pubsub,
  ) {
    this.pubSub.subscribe(bbEventToggleAccountFavoriteUpdate, this.retriggerGetAccounts.bind(this));
  }

  private readonly requestObject = new ReplaySubject<GetProductsummaryRequest>(1);
  private readonly favoriteRequestComplete = new BehaviorSubject<boolean>(true);
  readonly updateError: BehaviorSubject<AccountsOverviewError | undefined> = new BehaviorSubject<
    AccountsOverviewError | undefined
  >(undefined);
  readonly error: BehaviorSubject<AccountsOverviewError | undefined> = new BehaviorSubject<
    AccountsOverviewError | undefined
  >(undefined);
  readonly loading = new BehaviorSubject<boolean>(true);
  private readonly aggregatedBalancesErrorSubject = new BehaviorSubject<HttpErrorResponse | undefined>(undefined);
  readonly aggregatedBalancesError = this.aggregatedBalancesErrorSubject.asObservable();
  private readonly loadingAggregatedBalancesSubject = new BehaviorSubject<boolean>(false);
  readonly loadingAggregatedBalances = this.loadingAggregatedBalancesSubject.asObservable();
  private readonly refreshAggregatedBalancesSubject = new BehaviorSubject<boolean>(false);

  readonly accountsList: Observable<Accounts | undefined> = combineLatest([
    this.requestObject,
    this.favoriteRequestComplete,
  ]).pipe(
    distinctUntilChanged(),
    tap(() => this.loading.next(true)),
    switchMap(([requestObject]) => this.getAccounts(requestObject)),
    scan(
      (acc: Accounts, curr: Accounts): Accounts => ({
        count: curr.count,
        items: this.mergeResponses(acc, curr),
        params: curr.params,
      }),
    ),
    tap(() => this.error.next(undefined)),
    catchError((error: AccountsOverviewError) => {
      this.error.next(error);
      return of(undefined);
    }),
    tap(() => this.loading.next(false)),
  );

  readonly aggregatedBalances: Observable<AccountAggregatedBalanceCurrency[] | undefined> = combineLatest([
    this.refreshAggregatedBalancesSubject,
  ]).pipe(
    tap(() => this.loadingAggregatedBalancesSubject.next(true)),
    switchMap(() => this.getAggregatedBalances()),
    tap(() => this.loadingAggregatedBalancesSubject.next(false)),
  );

  getAccountById(arrangementId: string): Observable<AccountArrangementItem> {
    return this.accountDataService.getArrangementById({ arrangementId });
  }
  /**
   * Fetches an unmasked attribute for the given `UnmaskedAttributeRequestParams`
   *
   * @param requestParameters
   */
  getUnmaskedAttribute(requestParameters: UnmaskedAttributeRequestParams): Observable<string> {
    return this.accountDataService.unmaskedAttribute(requestParameters, 'body', false, {
      httpHeaderAccept: 'text/plain',
    });
  }

  toggleAccountFavorite(
    body: AccountUserPreferences,
    errorTemplateRef: TemplateRef<any> | string,
    successTemplateRef: TemplateRef<any> | string,
  ): Observable<any | undefined> {
    return this.putAccountFavorite(body).pipe(
      take(1),
      tap(() => {
        this.showNotification(successTemplateRef, 'success');
        this.updateError.next(undefined);
        this.pubSub.publish(bbEventToggleAccountFavoriteUpdate, undefined);
      }),
      catchError((error: AccountsOverviewError) => {
        this.updateError.next(error);
        this.showNotification(errorTemplateRef, 'error');
        return of(undefined);
      }),
    );
  }

  private putAccountFavorite(accountUserPreferences: AccountUserPreferences): Observable<HttpResponse<any>> {
    return this.accountDataService.updateUserPreferences({ accountUserPreferences }).pipe(
      catchError((error: HttpErrorResponse) => {
        throw parseError(error);
      }),
    );
  }

  private getAccounts(requestObject: GetProductsummaryRequest): Observable<Accounts> {
    const paginationType = requestObject.paginationType || '';
    const from = requestObject.from || 0;
    const params = { ...requestObject };
    delete params.paginationType;
    return this.productSummaryDataService.getArrangementsByBusinessFunction(params, HttpResponseType.RESPONSE).pipe(
      map(res => this.mapResponseWithCount(res, { from, paginationType })),
      catchError((error: HttpErrorResponse) => {
        throw parseError(error);
      }),
    );
  }

  private mapResponseWithCount(
    response: HttpResponse<ProductSummaryItem[]>,
    { from, paginationType }: { from: number; paginationType: PaginationType | string },
  ): Accounts {
    // eslint-disable-next-line no-null/no-null
    if (response.body === null) {
      throw new Error();
    }
    const headerCount = response.headers ? response.headers.get('x-total-count') : undefined;
    const counter = headerCount ? parseInt(headerCount, 10) : response.body.length || 0;

    return {
      count: counter,
      items: response.body || [],
      params: { from, paginationType },
    };
  }

  private mergeResponses(acc: Accounts, current: Accounts): ProductSummaryItem[] {
    if (
      acc &&
      acc.items &&
      current &&
      current.items &&
      acc.params.paginationType === PaginationType.LOAD_MORE &&
      current.params.from !== 0
    ) {
      return [...acc.items, ...current.items];
    }
    return current.items;
  }

  private showNotification(templateRef: TemplateRef<any> | string, modifier: 'success' | 'error') {
    this.notificationService.showNotification({
      header: templateRef,
      modifier,
      message: '',
    });
  }

  getAccountsFrom(requestObject: Observable<GetProductsummaryRequest>) {
    requestObject.subscribe(this.requestObject);
  }

  refreshAggregatedBalances() {
    this.refreshAggregatedBalancesSubject.next(true);
  }

  private getAggregatedBalances(): Observable<AccountAggregatedBalanceCurrency[] | undefined> {
    const requestParameters = {};
    return this.balancesHttpService.getAggregations(requestParameters).pipe(
      map(response => this.mapResponseToAggregatedBalances(response)),
      tap(() => this.aggregatedBalancesErrorSubject.next(undefined)),
      catchError(error => this.parseAggregatedBalanceError(error)),
    );
  }

  private mapResponseToAggregatedBalances(aggregations: AggregatedBalances[]) {
    const allBalances = (aggregations || [{}])[0];
    return allBalances && allBalances.aggregatedBalances;
  }

  private parseAggregatedBalanceError(error: HttpErrorResponse) {
    this.aggregatedBalancesErrorSubject.next(error);
    return of(undefined);
  }

  private retriggerGetAccounts() {
    this.favoriteRequestComplete.next(true);
  }
}
