import { AfterViewInit, ChangeDetectorRef, Component, ElementRef, HostListener, Input } from '@angular/core';
import { SIZE_MOBILE_DEFAULT, SIZE_TABLET_DEFAULT } from './constants';
import { ResponsiveView } from './types';

@Component({ template: '' })
export class ResponsiveContainerComponent implements AfterViewInit {
  @Input()
  get responsiveView(): ResponsiveView {
    return this._customResponsiveView || this._responsiveView;
  }

  set responsiveView(value: ResponsiveView) {
    this._customResponsiveView = value;
  }
  get sizeMobile(): number {
    return this._sizeMobile;
  }

  set sizeMobile(value: number) {
    this._sizeMobile = value;
  }
  get sizeTablet(): number {
    return this._sizeTablet;
  }

  set sizeTablet(value: number) {
    this._sizeTablet = value;
  }
  /**
   * Specifies screen size: `mobile` | `tablet` | 'screen'
   *
   * @default 'screen'
   */
  private _responsiveView: ResponsiveView = 'screen';
  private _customResponsiveView: ResponsiveView | undefined;
  private _element: ElementRef<HTMLElement>;
  private _changeDetector: ChangeDetectorRef;
  private _sizeTablet = SIZE_TABLET_DEFAULT;
  private _sizeMobile = SIZE_MOBILE_DEFAULT;

  constructor(element: ElementRef<HTMLElement>, changeDetector: ChangeDetectorRef) {
    this._element = element;
    this._changeDetector = changeDetector;
  }

  /**
   * @internal
   */
  ngAfterViewInit() {
    if (!this._customResponsiveView) {
      this.checkElementSize();
      this._changeDetector.detectChanges();
    }
  }

  /**
   * Event handler on `windows:resize`
   * Checks size of the container
   */
  @HostListener('window:resize', [])
  onResize() {
    if (!this._customResponsiveView) {
      this.checkElementSize();
    }
  }

  /**
   * Checks width of the parent container and updates `isSmallScreen$` and `isExtraSmallScreen$`
   */
  checkElementSize() {
    const containerWidth = this._element.nativeElement?.firstElementChild?.getBoundingClientRect().width || 0;
    this._responsiveView = this.getResponsiveView(containerWidth);
  }

  private getResponsiveView(containerWidth: number): ResponsiveView {
    let responseView: ResponsiveView = 'screen';
    if (containerWidth <= this.sizeMobile) {
      responseView = 'mobile';
    } else if (containerWidth <= this.sizeTablet) {
      responseView = 'tablet';
    }
    return responseView;
  }
}
