import { Pipe, PipeTransform } from '@angular/core';
import { ExternalProductKindItem, SummaryProductKindItem, ProductKindItem } from '@backbase/data-ang/arrangements';
import { ProductKindUri } from '../../model/types';
import { isDefined } from '../../helpers/helpers';

/**
 * Verifies if "kind" of productKindItem is equal to expected "kind"
 *
 * @usageNotes
 * ### Example
 * ```html
 * <div *ngIf="productKind | bbIsProductKind:expectedProductKindType"></div>
 * ```
 */
@Pipe({ name: 'bbIsProductKind' })
export class IsProductKindPipe implements PipeTransform {
  /**
   * Verifies if "kind" of productKindItem is equal to expected "kind"
   *
   * @param productKindItem
   * @param expectedProductKindType
   */
  transform(
    productKindItem: ExternalProductKindItem | SummaryProductKindItem | ProductKindItem | undefined,
    expectedProductKindType: ProductKindUri | ProductKindUri[],
  ): boolean {
    if (isDefined(productKindItem?.kindUri)) {
      return Array.isArray(expectedProductKindType)
        ? expectedProductKindType.includes(productKindItem?.kindUri as ProductKindUri)
        : productKindItem?.kindUri === expectedProductKindType;
    }
    return false;
  }
}
