import { DebugElement } from '@angular/core';
import { ComponentFixture } from '@angular/core/testing';
import { By } from '@angular/platform-browser';
import { isDefined } from './helpers';

export class TestHelper<T> {
  private _fixture: ComponentFixture<T>;

  constructor(fixture: ComponentFixture<T>) {
    this._fixture = fixture;
  }

  getElement(locator: string): DebugElement | null {
    return this._fixture.debugElement.query(By.css(locator));
  }

  getElements(locator: string): DebugElement[] | null {
    return this._fixture.debugElement.queryAll(By.css(locator));
  }

  getText(locator: string): string | undefined {
    const element: DebugElement | null = this.getElement(locator);
    if (element) {
      return element.nativeElement.textContent.trim();
    }
    return undefined;
  }

  isElementPresent(locator: string): boolean {
    return isDefined(this.getElement(locator));
  }
}
